import React,{useState} from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import authService from "./services/authServices";
import Link from '@material-ui/core/Link';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';


function DialogFileUpload(props) {
    const [nameT, setNameT] = useState(authService.getJSONFilename(0)===null ? "No file uploaded" : authService.getJSONFilename(0) );
    const hiddenFileInput = React.useRef(null);
    const [files, setFiles] = useState("");
    const [errorJSON, setErrorJSON] = useState(false);
    const [errorEmpty, setErrorEmptyN] = useState(false);
    const [open, setOpen] = useState(props.c_open);
    


    function isJsonString(str) {
        try {
            JSON.parse(str);
        } catch (e) {
            return false;
        }
        return true;
    }
    
    const handleCancel = (e) => {
        if(authService.isFileLoadedJSON(0)){
            props.setShowSettings(false);
            if((authService.getJSONFilename(0))){
                
                setNameT(authService.getJSONFilename(0));
            }

        }else{
            props.setShowWarningFile(true);
        }

       
        props.setShowSettings(false);
    
    }

    
    const handleChange = (e) => {
      
        const fileReader = new FileReader();


        if(e.target.files[0] !== undefined) {
            fileReader.readAsText(e.target.files[0], "UTF-8");
            fileReader.fileName = e.target.files[0].name;
            
            fileReader.onload = e => {
                if(isJsonString(e.target.result)){
                  
                    setFiles(e.target.result);
                    setNameT(e.target.fileName);
                    setErrorJSON(false);
                    setErrorEmptyN(false);
    
                }else{
                    setErrorJSON(true);
                }
            };
        }else{
            setErrorEmptyN(true);
        }
    
    }  
    
    const handleSubmission = (e) => {
     

        if(files===null || files===""){
            setErrorEmptyN(true);
            props.setShowSettings(true);
        }else{
            authService.saveJSON(0, files, nameT);
            props.setShowSettings(false);
            window.location.reload(false);

        }
       
        

	};

    const handleClickUpload = event => {
        hiddenFileInput.current.click();
      };

  return (
    <div>
         <Dialog
            open={props.c_open}
            onClose={props.close}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">{"Upload Reactions File"}</DialogTitle>
            <DialogContent>
            
                <div className="container-user">
                    
                    <div className="item-user">
                     
                        <DialogContentText id="alert-dialog-description">
                            {"Please upload your reactions file ("}
                            <Link
                                component="button"
                                variant="body2"
                                onClick={() => {
                                    window.open("../../NewsCollector/data/reactionsPersonal.json", "_blank");
                                }}
                                >
                                format JSON
                            </Link>
                            {"): " }
                            <br />
                            

                            <input type="file" onChange={handleChange} ref={hiddenFileInput} hidden/><br />
                            <Button
                                onClick={handleClickUpload}
                                variant="contained"
                                color="primary"
                                startIcon={<CloudUploadIcon />}
                            >
                                Select File
                            </Button>    {nameT}

                            <br /><br />
                          
                            {authService.getJSONFilename(0)===null ? "**Upload the reactions file to show your complete profile." : "" }<br />
                            {errorJSON ? <p style={{color:'red'}} >Error in JSON format, please review file</p>: ""}
                            {errorEmpty ? <p style={{color:'red'}} >No reaction file selected</p>: ""}
                            
                            
                        </DialogContentText>
                        
                       
                    </div>
                </div>
            </DialogContent>
            <DialogActions>
            <Button onClick={handleCancel} color="primary">
                Cancel
            </Button>
            <Button onClick={handleSubmission} color="primary" autoFocus disabled={errorJSON || errorEmpty ? true: false}>
                Finish
            </Button>
            </DialogActions>
        </Dialog>
    </div>
  )
}

export default DialogFileUpload